import React, { useContext, useState } from 'react';
import server from '../shared/server';
import '../App.css';
import ChatContext from '../context/chatContext';

const OT = require('@opentok/client');

function EzChatOpeningScreen(){
    /**
     * Pulls all functions and data from dataFunctions array in
     * ChatContext. Use dataFunctions.functionName to reference
    */
    const dataFunctions = useContext(ChatContext);
    
    const [videoData, setVideoData] = useState({});
    const [superSession, setSuperSession] = useState([]);
    function showYesNo() {
        dataFunctions.setInventoryBox( true );
        dataFunctions.setAdditionalFeatBox( true );
        dataFunctions.setInventoryList( true ); 
        window.setTimeout(()=>{
          document.getElementsByClassName("ez-chat-btn-tabs")[0].style.right= '612px';
        }, 25);
    
        dataFunctions.setDefaultCheckedVal(true);
        dataFunctions.setOpeningScreenBtn( true );
    }

    async function connectToChat()  {

        const res = {
            apiKey: '',
            token: '',
            sessionId: ''
        };
        server.get('/openTalkSession', res)
            .then(res => setVideoData(res))
            .then(console.log(videoData.data));
       
    }

    async function connectToChatforReal() {
        
        const res = {
            apiKey: '',
            token: '',
            sessionId: ''
        };
        server.get('/openTalkSession', res)
            .then(res => setVideoData(res))
            .then(console.log(videoData.data));
       

        const apiKey = videoData.data.videoSessionData.apiKey;
        const sessionId = videoData.data.videoSessionData.sessionId;
        const token = videoData.data.videoSessionData.token;
        var session = OT.initSession(apiKey, sessionId);
        
        setSuperSession(session);
        
        session.connect(token, function (error) {
            if (error) {
                console.log("Failed to connect: ", error.message);
            if (error.name === "OT_NOT_CONNECTED") {
                alert("You are not connected to the internet. Check your network connection.");
            }
            } else {
            console.log("Connected");
            session.publish();
            }
        });
        session.on("streamCreated", function(event){
            session.subscribe( event.stream );
        });
    }


    function connectToDealerVideoChat(){
        
        let payload = {
            apiKey : videoData.data.videoSessionData.apiKey,
            sessionId : superSession,
            token : videoData.data.videoSessionData.token
        };
        console.log(payload);
       
    }

    return(
    <>
        {dataFunctions.openingScreenBtn ?
        <div>
            <div className="" style={{marginTop:'8px'}}>
                <span className="bold-and-beautiful">{dataFunctions.setGreeting()},</span>
            </div>
            {dataFunctions.dealerData.map( p =>
            <form className="ez-chat-starting-button-container" key={dataFunctions.dealerData[0]}>
                <fieldset className="ez-chat-starting-button">
                    <label style={{fontSize:'10px'}} className="ez-chat-starting-label" htmlFor="ezChatBtnMessageCheck"> 
                        <dataFunctions.FontAwesomeIcon icon="comment"/>&nbsp;&nbsp;Connect with {dataFunctions.dealerInfoData.dealerName} sales team.
                    </label>
                    <input className="ez-chat-starting-radio" name="ez-chat-starting-btn" id="ezChatBtnMessageCheck" value="Messaging" type="radio" onChange={dataFunctions.onRadioOut} onClick={dataFunctions.switchOpeningScreenBtn}/>
                </fieldset> 
                {/* <fieldset className="ez-chat-starting-button">
                    <label style={{fontSize:'10px'}} className="ez-chat-starting-label" htmlFor="ezChatBtnInventoryCheck">
                        <dataFunctions.FontAwesomeIcon icon="car"/>&nbsp;&nbsp;Select a vehicle for purchase / lease.</label>
                    <input className="ez-chat-starting-radio" name="ez-chat-starting-btn" id="ezChatBtnInventoryCheck" value="Inventory" onChange={showYesNo} type="radio" onClick={dataFunctions.switchOpeningScreenBtn}/>
                </fieldset>
                <fieldset className="ez-chat-starting-button">
                    <label style={{fontSize:'10px'}} className="ez-chat-starting-label" htmlFor="ezChatBtnScheduleCheck">
                        <dataFunctions.FontAwesomeIcon icon="calendar-alt"/>&nbsp;&nbsp;Schedule a dealer visit.</label>
                    <input className="ez-chat-starting-radio" name="ez-chat-starting-btn" id="ezChatBtnScheduleCheck" value="Schedule" onChange={dataFunctions.toggleCal} type="radio" onClick={dataFunctions.switchOpeningScreenBtn}/>
                </fieldset>
               <fieldset className="ez-chat-starting-button">
                    <label style={{fontSize:'10px'}} className="ez-chat-starting-label" htmlFor="ezChatBtnSchedulef">
                        <dataFunctions.FontAwesomeIcon icon="calendar-alt"/>&nbsp;&nbsp;Schedule a dealer Test.</label>
                    <input className="ez-chat-starting-radio" name="ez-chat-starting-btn" id="ezChatBtnSchedulef" value="Schedule"  type="radio" onClick={connectToChatforReal}/>
                </fieldset>
                <fieldset className="ez-chat-starting-button">
                    <label style={{fontSize:'10px'}} className="ez-chat-starting-label" htmlFor="ezChatBtnSchedulef">
                        <dataFunctions.FontAwesomeIcon icon="calendar-alt"/>&nbsp;&nbsp;Schedule a dealer Test.</label>
                    <input className="ez-chat-starting-radio" name="ez-chat-starting-btn" id="ezChatBtnSchedulef" value="Schedule"  type="radio" onClick={connectToDealerVideoChat}/>
                </fieldset> 
                
                {dataFunctions.serviceBtn ?
                <fieldset className="ez-chat-starting-button">
                    <label style={{fontSize:'10px'}} className="ez-chat-starting-label" htmlFor="ezChatBtnScheduleService">
                        <dataFunctions.FontAwesomeIcon icon="wrench"/>&nbsp;&nbsp;Schedule a service appointment.</label>
                    <input className="ez-chat-starting-radio" name="ez-chat-starting-btn" id="ezChatBtnScheduleService" value="Schedule" onChange={dataFunctions.toggleCal} type="radio" onClick={dataFunctions.switchOpeningScreenBtn}/>
                </fieldset>
                :
                null
                }
                {dataFunctions.bodyShopBtn ?
                <fieldset className="ez-chat-starting-button">
                    <label style={{fontSize:'10px'}} className="ez-chat-starting-label" htmlFor="ezChatBtnScheduleBody">
                        <dataFunctions.FontAwesomeIcon icon="car-crash"/>&nbsp;&nbsp;Schedule body shop estimate.</label>
                    <input className="ez-chat-starting-radio" name="ez-chat-starting-btn" id="ezChatBtnScheduleBody" value="Schedule" onChange={dataFunctions.toggleCal} type="radio" onClick={dataFunctions.switchOpeningScreenBtn}/>
                </fieldset>
                :
                null
                }
                */}
            </form>
            )}
        </div>
        :
        null
        }
    </>
    );
}

export default EzChatOpeningScreen;