import React, { useContext, useState, useEffect } from 'react';
import '../App.css';
import { library } from '@fortawesome/fontawesome-svg-core'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCaretDown, faCaretUp, faTimes, faUserCircle, faLaughBeam, faPaperPlane, faAngleRight, faAngleLeft, faCalendarAlt, faVideo, faPhoneAlt, faTimesCircle, faFileExcel } from '@fortawesome/free-solid-svg-icons';
import ScrollIntoView from 'react-scroll-into-view';
import ReactTooltip from 'react-tooltip';
import ChatContext from '../context/chatContext';

library.add(faAngleRight, faCaretDown, faCaretUp, faTimes, faUserCircle, faLaughBeam, faPaperPlane, faAngleLeft, faCalendarAlt, faVideo, faPhoneAlt, faTimesCircle);

function EzChatChatScreen() {
  /**
   * Pulls all functions and data from dataFunctions array in
   * ChatContext. Use dataFunctions.functionName to reference
  */
  const dataFunctions = useContext(ChatContext);
  const [checkEmpty, setCheckEmpty] = useState('');
  const [phoneInput, setPhoneInput] = useState(false);
  const [vidChatBtn, setVidChatBtn] = useState(false);
  const [chatBtn, setChatBtn] = useState(false);
  const [nameCircle, setNameCircle] = useState(true);
  //console.log({dataFunctions});
  //const val = document.getElementById('ezChatNameInputField').getAttribute("value");
  useEffect(() => {
    dataFunctions.scrollItDown();
  }, []);

  // useEffect(()=>{

  //   async function validateName(){
      
  //     console.log('val means nothing to you');
  //     if(dataFunctions.userName == ''){
  //       dataFunctions.setNoName(false);
  //       console.log('val means nothing to you', dataFunctions.userName);
  //     }else{
  //       console.log(dataFunctions.userName);
  //       dataFunctions.sendMessage();
  //     }
  //   }
  //   validateName();
  //   },[])

  function validateName(){      
    console.log('val means nothing to you');
    if(dataFunctions.userName == ''){
      setNameCircle(false);
      console.log('val means nothing to you', dataFunctions.userName);
    }else{
      setNameCircle(true);
      console.log(dataFunctions.userName);
      dataFunctions.sendMessage();
      setChatBtn(true);
    }
  }

  function onSubmit(e) {
    e.preventDefault();
    setChatBtn(true);
  }

  function typeInProgress(e) {
    let v = e.target.value.trim();
    dataFunctions.sendTypeProgress(v);
  }

  function startVideoChat(){
    dataFunctions.callVideoApi();
    setVidChatBtn(true);
  }
  
  function endVideoChat(){
    dataFunctions.endVideoApi();
    setVidChatBtn(false);
  }

  function showPhoneInput(){
    setPhoneInput(!phoneInput);
  };
  
  function startChatting(){
    dataFunctions.sendMessage();
    setChatBtn(true);
  }

  return (
    <>
      <div>
      
        {vidChatBtn ?  
          <div onClick={endVideoChat} className="ez-chat-end-chat">End Video</div>
          :
          null
        }
        <div className="ez-chat-bottom" id="ezChatBottom">
          <div style={{display:'flex', alignItems:'center', justifyContent:'space-between'}}>
          {dataFunctions.ezStartChat ?
          <>
          
            <form
              className="form-inside-input"
              onSubmit={onSubmit}
              noValidate
              style={{display:'flex', marginLeft:5, alignItems:'center'}}
            >
              {nameCircle ?
              
              null
              
              :
              
              <>
                <FontAwesomeIcon icon="times-circle" className="ezMinimizeChat"  data-tip="Please fill out name" style={{color:"red"}} />
                <ReactTooltip place="left" />
              </>
              
              }
            <fieldset className="info-fieldsets" style={{paddingBottom: '5px', display:'flex', }}>
              <input id="ezChatNameInputField" type="text" className="ez-chat-name-field" placeholder="Name" style={{ margin: "0px" }} required="required" value={dataFunctions.userName} onChange={(e) => { dataFunctions.setUserName(e.target.value); }} />
              
            </fieldset>
          </form>
          
          </>
          :
          <div><p className="user-name-tag">Welcome&nbsp;{dataFunctions.userName}</p></div>
          }
          
          {chatBtn ?
          <div style={{display:'flex', justifyContent: 'flex-end', marginRight:7, alignContent:'center'}}>
            <div onClick={startVideoChat}>
              {/* <FontAwesomeIcon icon="video" className="ezMinimizeChat" style={{color:'#ffffff', marginRight:7,}} data-tip="Video Chat Enabled"/> */}
              <div style={{color:'#ffffff', fontWeight:'700', padding:2, fontSize:10, borderStyle:'solid', borderWidth:1, borderColor:'#ffffff', borderRadius:3, display:'flex', alignItems:'center'}} className="ezMinimizeChat" data-tip="Video Chat Enabled">
                <FontAwesomeIcon icon="video" className="ezMinimizeChat" style={{color:'#ffffff', marginRight:7,}} data-tip="Video Chat Enabled"/> 
                <p style={{margin:2}}>Face Chat</p>
              </div>
              <ReactTooltip place="left" />
            </div>
            {/*
            DO NOT DELETE!!!
            <div>
              <div onClick={showPhoneInput} style={{color:'#ffffff', fontWeight:'700', padding:2, fontSize:10, borderStyle:'solid', borderWidth:1, borderColor:'#ffffff', borderRadius:3 }} className="ezMinimizeChat" data-tip="Send SMS text message">
                SMS
              </div>
              <ReactTooltip place="left" />
            </div> */}
            
          </div>
          :
          null
          }
          
          </div>
          {phoneInput ?
            <div style={{display:'flex',alignItems:'center'}}>
              <fieldset className="info-fieldsets" style={{ paddingLeft: '5px', paddingBottom: '5px', width:'100%' }}>
                <input type="text" className="ez-chat-name-field" placeholder="Phone" style={{ margin: "0px" }} required="required" value={dataFunctions.userPhone} onChange={(e) => { dataFunctions.setUserPhone(e.target.value); }} />
              </fieldset>
              {/* <div onClick={dataFunctions.setPhoneNumber}>
                <FontAwesomeIcon icon="phone-alt" className="ezMinimizeChat" style={{color:'#ffffff', marginRight:7,}} data-tip="Video Chat Enabled"/>
              </div> */}
            </div>
          :
          null
          }
          <div className="ez-chat-input-container">
            <div style={{ position: 'relative', marginBottom: ' -3px' }}>
              <textarea wrap="hard" type="text" className="ez-chat-chat-input" placeholder="Message" cols="50" rows={3} value={dataFunctions.userMessage} onKeyUp={typeInProgress} onChange={(e) => { dataFunctions.setUserMessage(e.target.value); }}></textarea>
              {dataFunctions.noName ?
              <>
                <FontAwesomeIcon  icon="paper-plane" className="ezMinimizeChat chatSend" onClick={validateName} data-tip="Send Message" />
                <ReactTooltip place="left" />
              </>
              :
              <>
                <FontAwesomeIcon icon="paper-plane" className="ezMinimizeChat chatSend"  data-tip="Please fill out name" style={{color:"#999"}} />
                <ReactTooltip place="left" />
              </>
              }
            </div>
            {/*<div onClick={()=>{q.setShowCalendar(true)}} data-tip="Schedule dealership visit" className="ez-chat-message-button-icons">
                <p className="ez-chat-schedule-btn-text">Schedule Dealer Visit</p>
                <FontAwesomeIcon style={{ margin: ' 0 auto', fontSize: ' 1.5rem', textAlign: 'center' }} icon="calendar-alt" className="ezMinimizeChat" />
              </div>
          <ReactTooltip place="left"/>*/}
          </div>
        </div>
      </div>
    </>
  );
}

export default EzChatChatScreen;